"use client";

import { useState, useEffect } from "react";
import { useSearchParams } from "next/navigation";
import SearchBar from "@/components/SearchBar";
import Filters from "@/components/Filters";
import VenueCard from "@/components/VenueCard";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";

interface ApiVenue {
  id: string;
  name: string;
  description: string;
  city: string;
  state: string;
  category: string;
  capacity: number;
  price: number;
  images: string[];
  averageRating: number;
  reviewCount: number;
}

interface Venue {
  id: string;
  nome: string;
  localizacao: string;
  descricao: string;
  categoria: string;
  capacidade: number;
  preco: number;
  imagem: string;
  avaliacoes: number;
  nota: number;
}

interface ApiResponse {
  venues: ApiVenue[];
  pagination: {
    page: number;
    limit: number;
    total: number;
    totalPages: number;
  };
}

interface FilterState {
  categoria: string;
  dataInicio: string;
  dataFim: string;
  capacidade: number[];
  preco: number[];
  localizacao: string;
}

// Função para mapear categorias do frontend para o banco
const mapCategoryToApi = (category: string) => {
  const categoryMap: { [key: string]: string } = {
    'chacara': 'CHACARA',
    'restaurante': 'RESTAURANTE', 
    'salao': 'SALAO',
    'pousada': 'POUSADA',
    'clube': 'CLUBE',
    'espaco-rural': 'ESPACO_RURAL',
    'hotel': 'HOTEL',
    'outros': 'OUTROS'
  };
  return categoryMap[category] || category;
};

// Função para mapear categorias do banco para o frontend
const mapCategoryToDisplay = (category: string) => {
  const categoryMap: { [key: string]: string } = {
    'CHACARA': 'Chácara',
    'RESTAURANTE': 'Restaurante', 
    'SALAO': 'Salão',
    'POUSADA': 'Pousada',
    'CLUBE': 'Clube',
    'ESPACO_RURAL': 'Espaço Rural',
    'HOTEL': 'Hotel',
    'OUTROS': 'Outros'
  };
  return categoryMap[category] || category;
};

export default function ListingsPage() {
  const searchParams = useSearchParams();
  const [venues, setVenues] = useState<Venue[]>([]);
  const [pagination, setPagination] = useState({
    page: 1,
    limit: 12,
    total: 0,
    totalPages: 0,
  });
  const [searchQuery, setSearchQuery] = useState(searchParams?.get('q') || '');
  const [filters, setFilters] = useState<FilterState>({
    categoria: '',
    dataInicio: '',
    dataFim: '',
    capacidade: [10],
    preco: [50000],
    localizacao: ''
  });
  const [isLoading, setIsLoading] = useState(true);
  const [sortBy, setSortBy] = useState('relevancia');
  const [error, setError] = useState('');

  // Função para buscar venues da API
  const fetchVenues = async () => {
    setIsLoading(true);
    setError('');
    
    try {
      const params = new URLSearchParams();
      
      if (searchQuery) params.append('search', searchQuery);
      if (filters.categoria) params.append('category', mapCategoryToApi(filters.categoria));
      if (filters.localizacao) params.append('city', filters.localizacao);
      if (filters.capacidade[0] > 10) params.append('minCapacity', filters.capacidade[0].toString());
      if (filters.preco[0] < 50000) params.append('maxPrice', filters.preco[0].toString());
      params.append('page', pagination.page.toString());
      params.append('limit', pagination.limit.toString());

      const response = await fetch(`/api/venues?${params.toString()}`);
      
      if (!response.ok) {
        throw new Error('Erro ao buscar venues');
      }

      const data: ApiResponse = await response.json();
      
      // Mapear dados da API para o formato esperado pelo frontend
      const mappedVenues: Venue[] = data.venues.map(venue => ({
        id: venue.id,
        nome: venue.name,
        localizacao: `${venue.city}, ${venue.state}`,
        descricao: venue.description,
        categoria: mapCategoryToDisplay(venue.category),
        capacidade: venue.capacity,
        preco: venue.price,
        imagem: venue.images && venue.images.length > 0 
          ? venue.images[0] 
          : 'https://placehold.co/400x300?text=Imagem+não+disponível',
        avaliacoes: venue.reviewCount || 0,
        nota: venue.averageRating || 0,
      }));

      // Aplicar ordenação local
      let sortedVenues = [...mappedVenues];
      switch (sortBy) {
        case 'preco-menor':
          sortedVenues.sort((a, b) => a.preco - b.preco);
          break;
        case 'preco-maior':
          sortedVenues.sort((a, b) => b.preco - a.preco);
          break;
        case 'capacidade':
          sortedVenues.sort((a, b) => b.capacidade - a.capacidade);
          break;
        case 'avaliacao':
          sortedVenues.sort((a, b) => b.nota - a.nota);
          break;
        default:
          // Relevância (manter ordem da API)
          break;
      }

      setVenues(sortedVenues);
      setPagination(data.pagination);
    } catch (error) {
      console.error('Erro ao buscar venues:', error);
      setError('Erro ao carregar espaços. Tente novamente.');
      setVenues([]);
    } finally {
      setIsLoading(false);
    }
  };

  // Buscar venues quando filtros mudarem
  useEffect(() => {
    fetchVenues();
  }, [searchQuery, filters, pagination.page, sortBy]);

  const handleSearch = (query: string) => {
    setSearchQuery(query);
    setPagination(prev => ({ ...prev, page: 1 }));
  };

  const handleFiltersChange = (newFilters: FilterState) => {
    setFilters(newFilters);
    setPagination(prev => ({ ...prev, page: 1 }));
  };

  const handlePageChange = (newPage: number) => {
    setPagination(prev => ({ ...prev, page: newPage }));
    window.scrollTo({ top: 0, behavior: 'smooth' });
  };

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl lg:text-4xl font-bold mb-4">
          Encontre seu espaço ideal
        </h1>
        <p className="text-lg text-muted-foreground">
          {isLoading ? "Carregando..." : `${pagination.total} espaços disponíveis`}
          {searchQuery && ` para "${searchQuery}"`}
        </p>
      </div>

      {/* Search Bar */}
      <div className="mb-8">
        <SearchBar onSearch={handleSearch} />
      </div>

      {/* Filters */}
      <div className="mb-8">
        <Filters onFiltersChange={handleFiltersChange} />
      </div>

      {/* Sort Options */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4 mb-8">
        <div className="text-sm text-muted-foreground">
          {isLoading ? "Carregando..." : `${venues.length} resultados encontrados`}
        </div>
        <div className="flex items-center gap-2">
          <span className="text-sm text-muted-foreground">Ordenar por:</span>
          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
            className="text-sm border border-border rounded px-3 py-1 bg-background"
          >
            <option value="relevancia">Relevância</option>
            <option value="preco-menor">Menor preço</option>
            <option value="preco-maior">Maior preço</option>
            <option value="capacidade">Maior capacidade</option>
            <option value="avaliacao">Melhor avaliação</option>
          </select>
        </div>
      </div>

      {/* Error Message */}
      {error && (
        <div className="mb-8 p-4 bg-destructive/10 border border-destructive/20 rounded-lg">
          <p className="text-destructive">{error}</p>
          <Button 
            onClick={fetchVenues} 
            variant="outline" 
            size="sm" 
            className="mt-2"
          >
            Tentar novamente
          </Button>
        </div>
      )}

      {/* Results */}
      {isLoading ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {[...Array(6)].map((_, index) => (
            <Card key={index} className="animate-pulse">
              <div className="h-48 bg-muted rounded-t-lg"></div>
              <CardContent className="p-4 space-y-3">
                <div className="h-4 bg-muted rounded w-3/4"></div>
                <div className="h-3 bg-muted rounded w-1/2"></div>
                <div className="h-3 bg-muted rounded w-full"></div>
                <div className="h-3 bg-muted rounded w-2/3"></div>
              </CardContent>
            </Card>
          ))}
        </div>
      ) : venues.length > 0 ? (
        <>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {venues.map((venue) => (
              <VenueCard key={venue.id} venue={venue} />
            ))}
          </div>

          {/* Pagination */}
          {pagination.totalPages > 1 && (
            <div className="flex justify-center items-center gap-4 mt-12">
              <Button
                variant="outline"
                onClick={() => handlePageChange(pagination.page - 1)}
                disabled={pagination.page <= 1}
              >
                Anterior
              </Button>
              
              <div className="flex items-center gap-2">
                {Array.from({ length: Math.min(5, pagination.totalPages) }, (_, i) => {
                  const pageNum = i + 1;
                  return (
                    <Button
                      key={pageNum}
                      variant={pagination.page === pageNum ? "default" : "outline"}
                      size="sm"
                      onClick={() => handlePageChange(pageNum)}
                    >
                      {pageNum}
                    </Button>
                  );
                })}
              </div>

              <Button
                variant="outline"
                onClick={() => handlePageChange(pagination.page + 1)}
                disabled={pagination.page >= pagination.totalPages}
              >
                Próxima
              </Button>
            </div>
          )}
        </>
      ) : (
        <div className="text-center py-16">
          <div className="max-w-md mx-auto">
            <div className="w-24 h-24 bg-muted rounded-full flex items-center justify-center mx-auto mb-6">
              <div className="w-12 h-12 bg-muted-foreground/20 rounded-full"></div>
            </div>
            <h3 className="text-xl font-semibold mb-2">Nenhum espaço encontrado</h3>
            <p className="text-muted-foreground mb-6">
              Tente ajustar seus filtros ou termo de busca para encontrar mais opções.
            </p>
            <Button 
              onClick={() => {
                setSearchQuery('');
                setFilters({
                  categoria: '',
                  dataInicio: '',
                  dataFim: '',
                  capacidade: [10],
                  preco: [50000],
                  localizacao: ''
                });
              }}
              variant="outline"
            >
              Limpar filtros
            </Button>
          </div>
        </div>
      )}
    </div>
  );
}
