"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Slider } from "@/components/ui/slider";

interface FilterState {
  categoria: string;
  dataInicio: string;
  dataFim: string;
  capacidade: number[];
  preco: number[];
  localizacao: string;
}

interface FiltersProps {
  onFiltersChange?: (filters: FilterState) => void;
  initialFilters?: Partial<FilterState>;
}

export default function Filters({ onFiltersChange, initialFilters = {} }: FiltersProps) {
  const [filters, setFilters] = useState<FilterState>({
    categoria: initialFilters.categoria || "",
    dataInicio: initialFilters.dataInicio || "",
    dataFim: initialFilters.dataFim || "",
    capacidade: initialFilters.capacidade || [10],
    preco: initialFilters.preco || [1000],
    localizacao: initialFilters.localizacao || "",
  });

  const [isExpanded, setIsExpanded] = useState(false);

  const categorias = [
    { value: "chacara", label: "Chácaras" },
    { value: "restaurante", label: "Restaurantes" },
    { value: "salao", label: "Salões de Eventos" },
    { value: "pousada", label: "Pousadas" },
    { value: "clube", label: "Clubes" },
    { value: "espaco-rural", label: "Espaços Rurais" },
    { value: "hotel", label: "Hotéis" },
    { value: "outros", label: "Outros" },
  ];

  const handleFilterChange = (key: keyof FilterState, value: any) => {
    const newFilters = { ...filters, [key]: value };
    setFilters(newFilters);
    
    if (onFiltersChange) {
      onFiltersChange(newFilters);
    }
  };

  const clearFilters = () => {
    const clearedFilters: FilterState = {
      categoria: "",
      dataInicio: "",
      dataFim: "",
      capacidade: [10],
      preco: [1000],
      localizacao: "",
    };
    setFilters(clearedFilters);
    
    if (onFiltersChange) {
      onFiltersChange(clearedFilters);
    }
  };

  const hasActiveFilters = 
    filters.categoria || 
    filters.dataInicio || 
    filters.dataFim || 
    filters.localizacao ||
    filters.capacidade[0] !== 10 ||
    filters.preco[0] !== 1000;

  return (
    <Card className="shadow-md border-0">
      <CardHeader className="pb-4">
        <div className="flex items-center justify-between">
          <CardTitle className="text-lg">Filtros</CardTitle>
          <div className="flex items-center gap-2">
            {hasActiveFilters && (
              <Button 
                variant="ghost" 
                size="sm" 
                onClick={clearFilters}
                className="text-muted-foreground hover:text-foreground"
              >
                Limpar
              </Button>
            )}
            <Button
              variant="ghost"
              size="sm"
              onClick={() => setIsExpanded(!isExpanded)}
              className="md:hidden"
            >
              {isExpanded ? "Ocultar" : "Mostrar"}
            </Button>
          </div>
        </div>
      </CardHeader>
      
      <CardContent className={`space-y-6 ${!isExpanded ? "hidden md:block" : ""}`}>
        {/* Basic Filters - Always Visible */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          <div className="space-y-2">
            <Label htmlFor="categoria">Categoria</Label>
            <Select 
              value={filters.categoria} 
              onValueChange={(value) => handleFilterChange("categoria", value)}
            >
              <SelectTrigger>
                <SelectValue placeholder="Todas as categorias" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="todas">Todas as categorias</SelectItem>
                {categorias.map((categoria) => (
                  <SelectItem key={categoria.value} value={categoria.value}>
                    {categoria.label}
                  </SelectItem>
                ))}
              </SelectContent>
            </Select>
          </div>

          <div className="space-y-2">
            <Label htmlFor="localizacao">Localização</Label>
            <Input
              id="localizacao"
              type="text"
              placeholder="Cidade ou região"
              value={filters.localizacao}
              onChange={(e) => handleFilterChange("localizacao", e.target.value)}
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="dataInicio">Data de Início</Label>
            <Input
              id="dataInicio"
              type="date"
              value={filters.dataInicio}
              onChange={(e) => handleFilterChange("dataInicio", e.target.value)}
            />
          </div>

          <div className="space-y-2">
            <Label htmlFor="dataFim">Data de Fim</Label>
            <Input
              id="dataFim"
              type="date"
              value={filters.dataFim}
              onChange={(e) => handleFilterChange("dataFim", e.target.value)}
              min={filters.dataInicio}
            />
          </div>
        </div>

        {/* Advanced Filters */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 pt-4 border-t border-border">
          <div className="space-y-3">
            <Label>Capacidade Mínima: {filters.capacidade[0]} pessoas</Label>
            <Slider
              value={filters.capacidade}
              onValueChange={(value) => handleFilterChange("capacidade", value)}
              max={500}
              min={10}
              step={10}
              className="w-full"
            />
            <div className="flex justify-between text-xs text-muted-foreground">
              <span>10</span>
              <span>500+</span>
            </div>
          </div>

          <div className="space-y-3">
            <Label>Preço Máximo: R$ {filters.preco[0].toLocaleString('pt-BR')}</Label>
            <Slider
              value={filters.preco}
              onValueChange={(value) => handleFilterChange("preco", value)}
              max={50000}
              min={500}
              step={500}
              className="w-full"
            />
            <div className="flex justify-between text-xs text-muted-foreground">
              <span>R$ 500</span>
              <span>R$ 50.000+</span>
            </div>
          </div>
        </div>

        {/* Filter Summary */}
        {hasActiveFilters && (
          <div className="pt-4 border-t border-border">
            <div className="flex flex-wrap gap-2">
              <span className="text-sm text-muted-foreground">Filtros ativos:</span>
              {filters.categoria && (
                <span className="text-xs px-2 py-1 bg-primary/10 text-primary rounded-full">
                  {categorias.find(c => c.value === filters.categoria)?.label}
                </span>
              )}
              {filters.localizacao && (
                <span className="text-xs px-2 py-1 bg-primary/10 text-primary rounded-full">
                  {filters.localizacao}
                </span>
              )}
              {filters.dataInicio && (
                <span className="text-xs px-2 py-1 bg-primary/10 text-primary rounded-full">
                  A partir de {new Date(filters.dataInicio).toLocaleDateString('pt-BR')}
                </span>
              )}
              {filters.capacidade[0] !== 10 && (
                <span className="text-xs px-2 py-1 bg-primary/10 text-primary rounded-full">
                  Min. {filters.capacidade[0]} pessoas
                </span>
              )}
              {filters.preco[0] !== 1000 && (
                <span className="text-xs px-2 py-1 bg-primary/10 text-primary rounded-full">
                  Até R$ {filters.preco[0].toLocaleString('pt-BR')}
                </span>
              )}
            </div>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
