"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card } from "@/components/ui/card";

interface SearchBarProps {
  onSearch?: (query: string) => void;
  placeholder?: string;
}

export default function SearchBar({ 
  onSearch, 
  placeholder = "Buscar espaços para eventos..." 
}: SearchBarProps) {
  const [query, setQuery] = useState("");
  const [error, setError] = useState("");

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!query.trim()) {
      setError("Por favor, insira um termo de busca.");
      setTimeout(() => setError(""), 3000);
      return;
    }

    setError("");
    if (onSearch) {
      onSearch(query.trim());
    } else {
      // Fallback: redirect to listings with search query
      const searchParams = new URLSearchParams({ q: query.trim() });
      window.location.href = `/listings?${searchParams.toString()}`;
    }
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setQuery(e.target.value);
    if (error) setError(""); // Clear error when user starts typing
  };

  return (
    <Card className="p-6 shadow-lg border-0 bg-background/95 backdrop-blur">
      <form onSubmit={handleSearch} className="space-y-4">
        <div className="flex flex-col sm:flex-row gap-3">
          <div className="flex-1">
            <Input
              type="text"
              placeholder={placeholder}
              value={query}
              onChange={handleInputChange}
              className="h-12 text-base border-2 focus:border-primary"
              aria-label="Campo de busca"
            />
            {error && (
              <p className="text-destructive text-sm mt-2 flex items-center">
                <span className="inline-block w-1 h-1 bg-destructive rounded-full mr-2"></span>
                {error}
              </p>
            )}
          </div>
          <Button 
            type="submit" 
            size="lg" 
            className="h-12 px-8 text-base font-medium"
          >
            Buscar
          </Button>
        </div>
        
        {/* Quick Search Suggestions */}
        <div className="flex flex-wrap gap-2 pt-2">
          <span className="text-sm text-muted-foreground">Sugestões:</span>
          {["Chácara", "Restaurante", "Salão", "Casamento", "Aniversário"].map((suggestion) => (
            <button
              key={suggestion}
              type="button"
              onClick={() => {
                setQuery(suggestion);
                setError("");
              }}
              className="text-xs px-3 py-1 bg-secondary hover:bg-secondary/80 rounded-full transition-colors"
            >
              {suggestion}
            </button>
          ))}
        </div>
      </form>
    </Card>
  );
}
